import React, { useRef, useState } from 'react';
import { Container, Row, Col, Form, Button, FormLabel } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import './SellPage.css';

const SellPage = (props) => {
    const file = useRef(null);
    const [checked, setChecked] = useState(false)

    const handleFileChange = (event) => {
        file.current = event.target.files[0];
    }

    const handleChecked = (event) => {
        setChecked(event.target.checked);
    }

    let formInfo = (
        <>
            <Form.Row>
                <Form.Group as={Col} controlId="formGridName">
                    <Form.Label>H V TN</Form.Label>
                    <Form.Control placeholder="NGUYN VN A" />
                </Form.Group>
                <Form.Group as={Col} controlId="formGridEmail">
                    <Form.Label>EMAIL</Form.Label>
                    <Form.Control type="email" placeholder="sample@email.com" />
                </Form.Group>
            </Form.Row>
            <Form.Group controlId="formGridAddress1">
                <Form.Label>A CH</Form.Label>
                <Form.Control placeholder="123 ng in Bin Ph" />
            </Form.Group>
            <Form.Row>
                <Form.Group as={Col} controlId="formGridDistrict">
                    <Form.Label>QUN/ TH X</Form.Label>
                    <Form.Control placeholder="Qun 3"/>
                </Form.Group>

                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>THNH PH</Form.Label>
                    <Form.Control placeholder="H Ch Minh"/>
                </Form.Group>

                <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>ZIP</Form.Label>
                    <Form.Control placeholder="700000"/>
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="formCondition">
                    <Form.Label>TNH TRNG</Form.Label>
                    <Form.Control
                        as="select"
                        id="inlineFormCustomSelectPref"
                        custom
                    >
                        <option value="0">MI</option>
                        <option value="1">XI RI</option>
                    </Form.Control>
                </Form.Group>
                <Form.Group as={Col} controlId="formBug">
                    <FormLabel>NU KHUYT IM (Optional - Used)</FormLabel>
                    <Form.Control />
                </Form.Group>
            </Form.Row>
        </>
    );

    let formPics = (
        <>
            <Form.Row>
                <Form.Group as={Col} controlId="hop">
                    <Form.Label>HP</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="giay">
                    <Form.Label>GIY + NAMETAG</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="mui">
                    <Form.Label>MI GIY</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="de">
                    <Form.Label> GIY</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="mong">
                    <Form.Label>MNG GIY</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="ngoai">
                    <Form.Label>GC NGOI</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="trong">
                    <Form.Label>GC TRONG</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="tem">
                    <Form.Label>TEM</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
                <Form.Group as={Col} controlId="lot">
                    <Form.Label>LT GIY</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
            </Form.Row>
            <Form.Row>
                <Form.Group as={Col} controlId="bug">
                    <Form.Label>KHIM KHUYT (Nu c/ Xi ri)</Form.Label>
                    <Form.Control onChange={handleFileChange} type="file" />
                </Form.Group>
            </Form.Row>
        </>
    );

    return (
        <div className="sell-page">
            <img 
                alt="background"
                className="background" 
                src="assets/images/sell-page.jpg"
                style={{"position":"fixed", "zIndex":-1, objectFit:'cover', height: '100%', width: '100%'}}
            />
            <Container style={{alignItems:'center'}}>
                <Form>
                    <h1 className="mb-5">ng hnh cng VIGG</h1>
                    {formInfo}
                    <h2>NH TON DIN THEO TIU CHUN CA VIGG (*)</h2>
                    {formPics}
                    <h2>GI MONG MUN (N V: VND)</h2>
                    <Form.Control className="mb-5"/>
                    <Form.Check 
                        type="checkbox"
                        id="default-checkbox"
                        label="BN HIU RNG VIGG S THU 5% PH DCH V CHO MI T GIAO DCH"
                        style={{textAlign:'center'}}
                        onChange={handleChecked}
                    />
                    <Button 
                        className="mt-4"
                        variant="primary" 
                        type="submit" 
                        style={{float:'right'}}
                        size="lg"
                        disabled={!checked}
                    >
                        Submit
                    </Button>
                </Form>
                <Link to="/image-standards"><h6>(*) Bm vo y  xem tiu chun n hng!</h6></Link>
            </Container>
        </div>
    );
};

export default SellPage;
